<script>
  export default {
    data: () => ({
      users: {},
      rating: 5,
    }),
    methods: {
      GetUser () {
        if (this.$store.getters.isLoggedIn) {
            
            this.users = JSON.parse(localStorage.getItem('userLogin'))
        }
      }
    },
    mounted() {
      this.GetUser();
    },
  }
</script>